import chaobject from "@/components/script/chaobject";

export default {
  name: "user___center__star",
  mixins: [chaobject],
  data() {
    return {
      tab: "video",
      cha_datas: undefined,
    }
  },
  created() {
    this.show_chastar();
    this.set_title('我的收藏');
  },
  methods: {
    show_chastar: function () {
      this.$axios.get("/app/cha/star", {
        params: {
          'start': 0,
          'limit': 30,
          'uid': this.$store.state.user.userinfo.uid,
        }
      }).then((response) => {
        console.log("video star list:", response);
        this.cha_datas = response.data.datas;
        this.$forceUpdate();

      }).catch((error) => {
        console.log("error:", error);
      });
    },
  }
}
